import React, { useState } from "react";
import './planner.css'
import { Responsive, WidthProvider } from "react-grid-layout";
import "react-grid-layout/css/styles.css";
import "react-resizable/css/styles.css";

import chairImage from "./assets/images/chair.jpeg";
import tableImage from "./assets/images/table.jpeg";
import chairImage2 from "./assets/images/chair2.png";
import closet from "./assets/images/closet.png";

const objects = [
  { id: "1", image: tableImage, name: "Стол" },
  { id: "2", image: chairImage, name: "Стул" },
  { id: "3", image: chairImage2, name: "Кресло" },
  { id: "4", image: closet, name: "Шкаф" },
];

const ResponsiveGridLayout = WidthProvider(Responsive);

const Planner = () => {
  const [items, setItems] = useState([]);

  const handleAddItem = (obj) => {
    const newItem = {
      i: `new-${items.length + 1}`,
      x: 0,
      y: 0,
      w: 2,
      h: 2,
      image: obj.image,
      alt: obj.alt,
    };
    setItems([...items, newItem]);
  };

  const saveLayout = () => {
    const layout = items.map((item) => ({
      id: item.i,
      x: item.x,
      y: item.y,
      w: item.w,
      h: item.h,
      image: item.image,
      alt: item.alt,
    }));
    const blob = new Blob([JSON.stringify(layout)], {
      type: "application/json",
    });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.download = "layout.json";
    link.click();
  };

  const loadLayout = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = (event) => {
      const layout = JSON.parse(event.target.result);
      const loadedItems = layout.map((layoutItem) => ({
        i: layoutItem.id,
        x: layoutItem.x,
        y: layoutItem.y,
        w: layoutItem.w,
        h: layoutItem.h,
        image: layoutItem.image,
        alt: layoutItem.alt,
      }));
      setItems(loadedItems);
    };
    reader.readAsText(file);
  };

  return (
    <>
      <div className="object-choose-panel">
        <h3>Выберите объект:</h3>
        <div className="object-items">
        {objects.map((obj) => (
          <div className="object-item">
          <img
            key={obj.id}
            src={obj.image}
            alt={obj.name}
            onClick={() => handleAddItem(obj)}
          />
          <span>{obj.name}</span>
          </div>
        ))}
        </div>
      </div>

      <div className="buttons-wrapper">
        <button className="save-button" onClick={saveLayout}>
          Сохранить расстановку
        </button>
        <label htmlFor="file-upload" className="load-button">Загрузить расстановку</label>
        <input type="file" id="file-upload" onChange={loadLayout} accept=".json" />
        <button className="clear-button" onClick={() => setItems([])}>Очистить</button>
      </div>

      <ResponsiveGridLayout
        className="layout"
        compactType={null}
        autoSize={true} 
        rowHeight={50}
        width={1200}
        onLayoutChange={(layout) => {
          const updatedItems = items.map((item) => {
            const newLayout = layout.find((l) => l.i === item.i);
            return newLayout
              ? {
                  ...item,
                  x: newLayout.x,
                  y: newLayout.y,
                  w: newLayout.w,
                  h: newLayout.h,
                }
              : item;
          });
          setItems(updatedItems);
        }}
      >
        {items.map((item) => (
          <div
            key={item.i}
            data-grid={{ x: item.x, y: item.y, w: item.w, h: item.h }}
          >
            <img
              src={item.image}
              alt={item.alt}
              style={{ width: "100%", height: "100%", objectFit: "cover"}}
            />
          </div>
        ))}
      </ResponsiveGridLayout>
    </>
  );
};

export default Planner;
